<template>
  <div class="hmw">
    <!--  -->
    <!--  -->
    <van-overlay :show="show" @click="show = false">
      <div class="wrapper">
        <div class="block">
          <p></p>
          <div class="waw_img_pop">
            <img :src="imrUrl" />
          </div>
        </div>
      </div>
    </van-overlay>
    <!--  -->
    <van-sticky>
      <div class="hmw-top">
        <!--  -->
        <van-icon name="arrow-left" @click="onClickLeft" />
        <p v-show="!hmwFlag"></p>
        <div class="hmwNav" v-show="hmwFlag">
          <span
            :class="hmwIndex == 0 ? 'hmwActiveTop' : ''"
            @click="hmwDian(0)"
            id="hmwKc"
            ></span
          >
          <span
            :class="hmwIndex == 1 ? 'hmwActiveTop' : ''"
            @click="hmwDian(1)"
            id="hmwDg"
            ></span
          >
          <span
            :class="hmwIndex == 2 ? 'hmwActiveTop' : ''"
            @click="hmwDian(2)"
            id="hmwPj"
            ></span
          >
        </div>
        <van-icon name="cluster-o" @click="showPopup" />
      </div>
    </van-sticky>

    <!--  -->
    <div class="hmw-center" style="background: #f0f2f5">
      <!--  -->
      <van-list>
        <ol>
          <!--  -->
          <li class="hmwXQ">
            <div class="waw_hmwtop">
              <p class="hmwP1 hmwTOP1">
                -sass
              </p>
              <van-icon
                class="hmwNo"
                v-show="listInfo.is_collect == 0"
                name="star-o"
                @click="onClickStart(listInfo.is_collect, listInfo.collect_id)"
              />
              <van-icon
                class="hmwNo"
                v-show="listInfo.is_collect != 0"
                color="orange"
                name="star"
                @click="onClickStart(listInfo.is_collect, listInfo.collect_id)"
              />
            </div>
            <p class="hmwP2">
              {{ listInfo.price == 0 ? "" : "" + listInfo.price }}
            </p>
            <p class="hmwP3">
              {{ listInfo.is_free }} |
              {{ listInfo.real_sales_num }}
            </p>
          </li>
          <!--  -->
          <li class="hmwTD" v-for="(item, index) in listTeacher" :key="index">
            <p class="hmwP1"></p>
            <div>
              <img :src="item.teacher_avatar" alt="" /><span>{{
                item.teacher_name
              }}</span>
            </div>
          </li>
          <!--  -->
          <li class="hmwJS hmwSroll">
            <p class="hmwP1"></p>
          </li>
          <!--  -->
          <li class="hmwDG hmwSroll" style="padding-bottom: 2px">
            <p class="hmwP1"></p>
            <!--  -->
            <ul>
              <li :key="index" v-for="(item, index) in 3">
                <p>
                  <span class="hmwS1" style=""></span
                  ><span class="hmwS2"></span>
                </p>
                <p class="hmwP3">8 | 134</p>
              </li>
            </ul>
          </li>
          <!--  -->
          <li class="hmwPL hmwSroll">
            <p class="hmwP1"></p>
            <div v-show="listPJData.length==0" class="waw_empty_img_box">
              <img src="https://img.yzcdn.cn/vant/empty-image-default.png">
              <p></p>
            </div>
            <ul v-show="listPJData.length>0">
              <li :key="index" v-for="(item, index) in listPJData">
                <img
                  :src="item.avatar"
                />
                <div class="hmwPL-center">
                  <p class="hmwCP1">
                    <span>{{ item.nickname }}</span
                    ><van-rate
                      :size="14"
                      v-model="item.grade"
                      void-color="#eee"
                      color="rgb(234, 122, 47)"
                      readonly
                    />
                  </p>
                  <p class="hmwCP2">{{item.content}}</p>
                </div>
                <div class="hmwPL-right">{{item.created_at | filterTime}}</div>
              </li>
            </ul>
          </li>
        </ol>
      </van-list>
    </div>
    <!--  -->
    <van-tabbar>
      <div class="hmw-foot">
        <van-button
          v-show="listInfo.is_buy == 0"
          type="primary"
          block
          @click="hmwStudyJump"
          ></van-button
        >
        <van-button
          v-show="listInfo.is_buy == 1"
          type="primary"
          block
          @click="hmwStudyJump"
          ></van-button
        >
      </div>
    </van-tabbar>
  </div>
</template>

<script>
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import QRCode from "qrcode";
export default {
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    return {
      //    --
      active: 2,
      startActive: 4,
      //   
      hmwFlag: false,
      // 
      hmwIndex: 0,
      //   
      hmwShow: false,
      // 
      hmwBtnFlag: false,
      // 
      show: false, //
      imrUrl: "", // 
      // 
      hmwSc: true,
      listDate: [], //
      listInfo: [], //
      listTeacher: [], //
      listPJData:[],//
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  filters:{
    filterTime(val){
       let date = new Date(val*1000);
      let y = date.getFullYear();
      let m = (date.getMonth() + 1)>9?(date.getMonth() + 1):'0'+(date.getMonth() + 1);
      let d = date.getDate()>9?date.getDate():'0'+date.getDate();
      let h = date.getHours()>9?date.getHours():'0'+date.getHours();
      let mm = date.getMinutes()>9?date.getMinutes():'0'+date.getMinutes();
      let s = date.getSeconds()>9?date.getSeconds():'0'+date.getSeconds();

      let time = `${y}-${m}-${d} ${h}:${mm}:${s}`;
      return time;
    }
  },
  // 
  methods: {
    //   nav
    scrollHandle: function (e) {
      var top = e.srcElement.scrollingElement.scrollTop; // 
      if (top > 0) {
        this.hmwFlag = true;
      } else {
        this.hmwFlag = false;
      }
    },
    //   nav
    hmwDian(index) {
      // // 
      this.hmwIndex = index;
      console.log(this.hmwIndex);
      //  offsetTop
      // css 1  0  +1
      const targetOffsetTop =
        document.querySelector(`.van-list>ol>li:nth-child(${index + 3})`)
          .offsetTop - 50;
      //  offsetTop
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //  50 
      const STEP = 50;
      // 
      if (scrollTop > targetOffsetTop) {
        // 
        smoothUp();
      } else {
        // 
        smoothDown();
      }
      // 
      function smoothDown() {
        //  scrollTop  targetOffsetTop 
        if (scrollTop < targetOffsetTop) {
          //  STEP  STEP
          // 
          if (targetOffsetTop - scrollTop >= STEP) {
            scrollTop += STEP;
          } else {
            scrollTop = targetOffsetTop;
          }
          // 

          document.body.scrollTop = scrollTop;
          document.documentElement.scrollTop = scrollTop;
          //  requestAnimationFrame  setInterval 
          requestAnimationFrame(smoothDown);
        }
      }
      // 
      function smoothUp() {
        if (scrollTop > targetOffsetTop) {
          if (scrollTop - targetOffsetTop >= STEP) {
            scrollTop -= STEP;
          } else {
            scrollTop = targetOffsetTop;
          }
          document.body.scrollTop = scrollTop;
          document.documentElement.scrollTop = scrollTop;
          requestAnimationFrame(smoothUp);
        }
      }
    },
    // 
    hmwStudyJump() {
      if (this.listInfo.is_buy == 0 && this.listInfo.price == 0) {
        this.$ClientAPI
          .baoming({
            shop_id: this.listInfo.id,
            type: 5,
          })
          .then((res) => {
            console.log(res);
            if (res.data.code == 200) {
              this.getDetails();
            }
            var time = null;
            time = setTimeout(() => {
              Toast.success(res.data.msg);
              clearTimeout(time);
            }, 1000);
          })
          .catch((err) => {
            console.log(err);
          });
      } else if (this.listInfo.is_buy == 1) {
        this.$router.push({
          path: "/studyDetails",
          query: { id: this.listInfo.id },
        });
      } else if (this.listInfo.is_buy == 0 && this.listInfo.price != 0) {
        this.$router.push({
          path: "/confirmOrder",
          query: {
            id: this.listInfo.id,
          },
        });

        Toast.success("");
      }

      document.documentElement.scrollTop = 0;
    },
    // 
    showPopup() {
      this.show = true;
      let url = location.href;
      // console.log(url);
      QRCode.toDataURL(url)
        //dataimrUrl
        .then((tpian) => {
          // console.log(tpian);
          this.imrUrl = tpian;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    // 
    onClickStart(id, collect_id) {
      // console.log(id);
      if (id == 0) {
        var obj = {
          course_basis_id: this.$route.query.id,
          type: 1,
        };
        this.$ClientAPI
          .kechengShow(obj)
          .then((res) => {
            // console.log(res.data)
            if (res.data.code == 200) {
              this.getDetails();
            }
            var time = null;
            time = setTimeout(() => {
              Toast.success("");
              clearTimeout(time);
            }, 1000);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$ClientAPI
          .keChengHide(collect_id)
          .then((res) => {
            // console.log(res.data.code)
            if (res.data.code == 200) {
              this.getDetails();
            }
            var strTime = null;
            strTime = setTimeout(() => {
              Toast.success("");
              clearTimeout(strTime);
            }, 1000);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 
    onScroll() {
      // 
      const navContents = document.querySelectorAll(".hmwSroll");
      // console.log(navContents)
      //  offsetTop
      const offsetTopArr = [];
      navContents.forEach((item) => {
        offsetTopArr.push(item.offsetTop);
      });
      //  scrollTop
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // console.log(scrollTop)
      // 
      let navIndex = 0;
      for (let n = 0; n < offsetTopArr.length; n++) {
        //  scrollTop n offsetTop  n-1 
        // n
        if (scrollTop >= offsetTopArr[n] - 50) {
          navIndex = n;
        }
      }
      // 
      this.hmwIndex = navIndex;
      // console.log(this.hmwIndex)
      this.active = navIndex;
    },

    // 
    onClickLeft() {
      this.$router.go(-1);
    },
    getDetails() {
      // 
      this.$ClientAPI
        .courseInfo(this.$route.query.id)
        .then((res) => {
          console.log(res.data.data);
          this.listDate = res.data.data;
          this.listInfo = res.data.data.info;
          this.listTeacher = res.data.data.teachers;
          // console.log(res.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    document.documentElement.scrollTop = 0;
    window.addEventListener("scroll", this.scrollHandle); // 
    // 
    window.addEventListener("scroll", this.onScroll, false);
    // 
    this.getDetails();

    // //   
    // var obj = {
    //   teacher_id: sessionStorage.getItem("teacherId"),
    //   limit: 10,
    //   page: 1,
    // };
    // this.$ClientAPI
    //   .PINGJIA(obj)
    //   .then((res) => {
    //     // console.log(res.data);
    //   })
    //   .catch((err) => {
    //     console.log(err);
    //   });

      // console.log(this.$route.query.id);
    // 
    var strObj = {
      id: this.$route.query.id,
      limit: 10,
      page: 1,
    };
    this.$ClientAPI
      .KCpingLun(strObj)
      .then((res) => {
        console.log(res.data.data.list);
        this.listPJData = res.data.data.list
      })
      .catch((err) => {
        console.log(err);
      });
  },
  destroy() {
    // vue
    window.removeEventListener("scroll", this.onScroll);
  },
};
</script>
<style scoped>
